.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list-none {
  list-style: none;
  padding: 0;
  margin: 0;
}
.button-none {
  background: none;
  border: none;
  padding: 0;
}
.container {
  padding: 0 19px;
  width: calc(100vw - (19px * 2));
  max-width: calc(100vw - (19px * 2));
  margin: 0 auto;
}
.body {
  min-height: 100%;
  margin: 0;
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  background-color: #ffffff;
  color: #333333;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  overflow-x: clip;
}
.main-header {
  min-height: 56px;
  position: relative;
  padding: 17px 0px;
  box-sizing: border-box;
  max-width: 500px;
  margin: 0 auto;
}
.main-header .container {
  max-width: 100%;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .main-header {
    max-width: 885px;
  }
}
@media (min-width: 1280px) {
  .main-header {
    max-width: 100vw ;
  }
}
.main-header__button {
  background: none;
  border: none;
  padding: 0;
  width: 23px;
  height: 22px;
  background: url("../../img/icon-menu.svg.svg");
  cursor: pointer;
  margin-left: auto;
  display: block;
}
@media (min-width: 768px) {
  .main-header__button {
    display: none;
  }
}
.main-menu__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  transition: 0.7s;
}
.main-menu__list--open {
  top: 0 !important;
  z-index: 9998;
  transition: 1s;
}
@media (max-width: 767px) {
  .main-menu__list {
    position: fixed;
    top: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    background: #81b3d2;
    left: 0;
    align-items: center;
    top: -50vh;
  }
}
.main-menu__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
}
@media (max-width: 767px) {
  .main-menu__item {
    box-shadow: 0px 1px 0px rgba(1, 1, 1, 0.2), inset 0px 1px 0px rgba(255, 255, 255, 0.2);
    padding-top: 15px;
    padding-bottom: 15px;
    width: 100%;
  }
}
.main-menu__link {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
}
@media (max-width: 767px) {
  .main-menu__link {
    color: white !important ;
  }
}
.main-menu__link--link {
  color: #766357;
}
.main-menu__container {
  display: flex;
  gap: 120px;
}
@media (max-width: 767px) {
  .main-menu__container {
    display: flex;
    flex-direction: column;
    gap: 0;
    text-align: center;
    width: 100%;
  }
}
@media (min-width: 1280px) {
  .main-menu__container {
    display: flex;
    gap: 180px;
  }
}
@media (min-width: 1280px) {
  .main-menu {
    padding: 0 91px;
  }
}
.main-menu__close {
  background: none;
  border: none;
  padding: 0;
  background: url("../../img/icon-cross.svg");
  cursor: pointer;
  position: absolute;
  width: 22px;
  height: 22px;
  background-size: contain;
  right: 16px;
  top: 17px;
}
.first-screen__main-text {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 35px;
  color: #000000;
}
@media (min-width: 768px) {
  .first-screen__main-text {
    font-size: 21px;
    max-width: 520px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 47.5px;
  }
}
@media (min-width: 1280px) {
  .first-screen__main-text {
    max-width: 483px;
  }
}
.first-screen__text {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: #333333;
}
@media (min-width: 768px) {
  .first-screen__text {
    font-size: 14px;
    max-width: 520px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 56.5px;
  }
}
@media (min-width: 1280px) {
  .first-screen__text {
    max-width: 483px;
  }
}
.first-screen {
  padding-bottom: 16px;
}
.first-screen__img {
  margin-bottom: 32px;
}
.first-screen__image {
  width: 100%;
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .first-screen__image--tablet {
    display: block;
    width: 100%;
  }
}
@media (min-width: 1280px) {
  .first-screen__image--tablet {
    display: none;
    width: 100%;
  }
  .first-screen__image--desktop {
    width: 100%;
  }
}
.benefits__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.benefit__text-container {
  text-align: center;
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  padding-top: 54px;
  padding-bottom: 40px;
  color: white;
  background-color: #81b3d2;
  position: relative;
  width: 100%;
  box-sizing: border-box;
}
.benefit__text-container--first::after {
  content: "";
  position: absolute;
  width: 100vw;
  height: 37px;
  background: url("../../img/Group\ 4\ \(1\).svg") no-repeat;
  top: 0;
  background-size: cover;
  left: 0;
  z-index: 1;
}
@media (min-width: 768px) {
  .benefit__text-container--first::after {
    content: none;
  }
}
.benefit__text-container--right {
  order: 1;
}
.benefit__text-container--gray {
  box-sizing: border-box;
  height: 100%;
  border-bottom: 1px solid #d6d6d6;
  background-color: #eeeeee;
  color: #000000;
}
.benefit__text-container--gray-last {
  border: none;
}
.benefit__text {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  max-width: 80%;
  margin: 0 auto;
  width: fit-content;
}
.benefit__text:not(:last-child) {
  margin-bottom: 17px;
}
.benefit__title {
  margin: 0;
  margin-bottom: 11px;
}
.benefit__image {
  width: 100%;
  margin: 0 auto;
  display: block;
}
@media (min-width: 768px) {
  height: 100%;
  max-width: none;
  order: -1;
  --right {
    order: 1;
    display: flex;
  }
}
@media (min-width: 768px) {
  .benefit__general-information-container {
    display: grid;
    height: 256px;
    grid-template-columns: 2.5fr 1fr;
    overflow: hidden;
  }
  .benefit__general-information-container--first {
    display: grid;
    grid-template-columns: 1fr 2.5fr;
  }
}
@media (min-width: 768px) {
  .benefit__tablet-container {
    display: flex;
  }
}
@media (min-width: 1280px) {
  .benefit__tablet-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .benefit__tablet-container .benefit {
    width: 100%;
  }
  .benefit__tablet-container .benefit__text-container {
    width: 100%;
  }
}
.benefit-discription__list {
  list-style: none;
  padding: 0;
  margin: 0;
  padding-left: 48px;
}
@media (min-width: 768px) {
  .benefit-discription__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    padding: 0;
  }
}
.benefit-discription {
  padding: 0 18px;
  width: calc(100vw - (18px * 2));
  margin: 0 auto;
  max-width: calc(100vw - (18px * 2));
  padding-top: 31px;
  padding-bottom: 52px;
}
@media (min-width: 768px) {
  .benefit-discription {
    max-width: 885px;
    box-sizing: border-box;
  }
}
.benefit-discription__title {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  margin: 0;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .benefit-discription__title {
    font-size: 21px;
  }
}
.benefit-discription__text {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
  margin: 0;
}
@media (min-width: 768px) {
  .benefit-discription__text {
    font-size: 21px;
  }
}
.benefit-disription__item {
  position: relative;
}
.benefit-disription__item::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  left: -48px;
  top: -10px;
  background-size: contain;
}
@media (min-width: 768px) {
  .benefit-disription__item::before {
    width: 75px;
    height: 72px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
.benefit-disription__item:nth-child(1)::before {
  background: url(../../img/icon-house.svg.svg) no-repeat center center;
  background-size: contain;
}
.benefit-disription__item:not(:last-child) {
  margin-bottom: 30px;
}
.benefit-disription__item:nth-child(2)::before {
  background: url(../../img/icon-burger.svg.svg) no-repeat center center;
  background-size: contain;
}
.benefit-disription__item:nth-child(3)::before {
  background: url(../../img/icon-gift.svg.svg) no-repeat center center;
  background-size: contain;
}
@media (min-width: 768px) {
  .benefit-disription__item {
    text-align: center;
    padding-top: 60px;
    padding-top: 98px;
  }
}
.ca-block__title {
  margin: 0;
  margin-bottom: 21px;
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
